import * as React from "react";

export default function BookImpressions() {
  const [count, setCount] = React.useState(0);
  return (
    <div>
      <button onClick={() => setCount((s) => s + 1)}>+</button>
      {count}
      <button onClick={() => setCount((s) => s - 1)}>-</button>
    </div>
  );
}
